import { Component, Input } from '@angular/core';
import { Question, QuestionStat, Collections } from '../../app/app.models';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AngularFirestore } from 'angularfire2/firestore';
import { ViewController } from 'ionic-angular';

@Component({
  selector: "exam-feedback-modal",
  templateUrl: "exam-feedback-modal.html"
})
export class ExamFeedbackModalComponent {

  public q: Question;
  public stats: { q: Question; stat$: Observable<QuestionStat> }[] = [];

  @Input()
  public set lastQuestion(qs: Question[]) {
    this.stats = [];

    for (const q of qs) {
      let payload = {
        q,
        stat$: this.afs
          .collection(Collections.QUESTION_STAT)
          .doc<QuestionStat>(q.id)
          .valueChanges()
          .pipe(
            map(stat =>
              stat
                ? { ...stat, stat: Object.entries(stat.stat) }
                : {
                    id: `${Collections.QUESTION_STAT}/${q.id}`,
                    question: q,
                    stat: [],
                    total: 0
                  }
            )
          )
      };

      this.stats.push(payload);
    }
  }
  public get lastQuestion(): Question[] {
    return [];
  }

  @Input() lastSelected: string;

  constructor(
    private afs: AngularFirestore,
    private viewCtrl: ViewController,
  ) {}

  ngOnInit() {}

  closeModal() {
    this.viewCtrl.dismiss();
  }

  filterStats(question: Question, stats: any[]) {
    const ans =
      question && question.respuestas
        ? question.respuestas.map(r => r.text)
        : [];
    return stats ? stats.filter(s => ans.includes(s[0])) : [];
  }

}



